import React, { useState, useEffect } from 'react'
import {
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CFormGroup
} from  '@coreui/react'
import CIcon from '@coreui/icons-react'
import { httpClient } from '../../helpers';
import ReactJson from 'react-json-view'

const Invoice = (props) => {
    const [fetch, setFetch] = useState(true);
    const [invoiceJson, setInvoiceJson] = useState({});
    const [invoiceImg, setInvoiceImg] = useState("")

    useEffect(() => {
        if(fetch) {
          const url = "/be/api/transaction/svg/" + props.match.params.id ;
          httpClient
            .get(url)
            .then((res) => {
              setInvoiceImg(res.data.svg);
              setInvoiceJson(JSON.parse(res.data.json));
            })
            .catch((error) => {
              console.error("Error", error);
            });
          
          setFetch(false);
        }

      },[fetch]);

  return (
    <>
      <CRow>
        <CCol xs="12" sm="12" md="12">
          <CCard>
            <CCardHeader>
              Invoice
            </CCardHeader>
            <CCardBody>
                <img src={invoiceImg}/>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </>
  )
}

export default Invoice
